<!-- by 添少 -->
<template>
  <fixed-layout>
    <template slot="header">
      <topbar>
        <template slot="before">
          <b class="iconfont icon-back" @click="$router.go(-1)"></b>
        </template>
        发帖
        <template slot="after">
          <button class="btn" @click="handNext">发送</button>
        </template>
      </topbar>
    </template>
    <section class="m-write">
      <div id="editor"></div>
    </section>
    <section class="m-uploads">
      <template v-for="(row,$index) in images">
        <div class="item">
          <img class="fit-cover" :src="row.src"/>
          <b class="iconfont icon-close" @click="handDeleteImage(row,$index)"></b>
        </div>
      </template>
      <div class="item">
        <file-image @load="handLoadFile"></file-image>
      </div>
    </section>
  </fixed-layout>
</template>
<script>
  import FixedLayout from '@/components/fixed-layout'
  import Topbar from '@/components/topbar'
  import FileImage from '@/components/file-image'
  import Quill from 'quill'
  import 'quill/dist/quill.bubble.css'

  let quill = null
  export default {
    name: '',
    components: {
      FixedLayout,
      Topbar,
      FileImage
    },
    props: {},
    data() {
      return {
        content: '',
        images: [],
        nextUrl: 'club'
      }
    },
    watch: {},
    computed: {},
    created() {
      this.init()
    },
    mounted() {
      quill = new Quill('#editor', {
        modules: {
          toolbar: [
            [{header: [1, 2, false]}],
            ['bold', 'italic', 'underline'],
            ['image']
          ]
        },
        formats: {
          size: '120px',
          bold: true,
        },
        placeholder: '请输入正文',
        theme: 'bubble'
      })
    },
    methods: {
      init() {
      },
      initData() {
      },
      handNext() {
        this.content = quill.getText()
        this.$store.commit(this.$actionType.write.set_post_data, {
          content: this.content,
          images: this.images
        })
        this.$router.push({
          path: this.nextUrl
        })
      },
      handLoadFile(event) {
        this.images.push({
          src: event.target.result
        })
      },
      handDeleteImage(row, index) {
        this.images.splice(index, 1)
      }
    }
  }
</script>
<style lang="scss">

</style>
<style lang="scss" scoped>
  .fixed-layout {
    background-color: #fff;
  }

  #editor {
    font-size: 16px;
  }

  .m-uploads {
    display: flex;
    flex-wrap: wrap;
    .item {
      height: 100px;
      margin-left: 10px;
      flex: 0 0 100px;
      border: solid #e5e5e5 1px;
      position: relative;
      b {
        background-color: rgba(0, 0, 0, 0.5);
        text-align: center;
        font-weight: 400;
        font-size: 8px;
        color: #fff;
        width: 20px;
        height: 20px;
        position: absolute;
        right: 0;
        top: 0;
      }
      &:last-child {
        border-width: 0;
      }
    }
  }

  .m-write {
    margin-bottom: 10px;
  }
</style>



